"use client";

import { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import { ChevronRight, CheckCircle, Clock } from "lucide-react";
import { Question } from "@/lib/questions";
import { useRouter } from "next/navigation";
import { submitQuizResult } from "@/app/actions";

interface QuizInterfaceProps {
    moduleId: string;
    questions: Question[];
}

export default function QuizInterface({ moduleId, questions }: QuizInterfaceProps) {
    const router = useRouter();
    const [currentIndex, setCurrentIndex] = useState(0);
    const [answers, setAnswers] = useState<Record<string, number>>({}); // questionId -> selectedIndex
    const [score, setScore] = useState(0);
    const [isFinished, setIsFinished] = useState(false);

    // Progress calculation
    const progress = ((currentIndex + 1) / questions.length) * 100;
    const currentQuestion = questions[currentIndex];

    const handleAnswer = (optionIndex: number) => {
        // Record answer
        const isCorrect = optionIndex === currentQuestion.correctIndex;

        setAnswers(prev => ({
            ...prev,
            [currentQuestion.id]: optionIndex
        }));

        if (isCorrect) {
            setScore(prev => prev + 1);
        }

        // Auto advance after short delay
        setTimeout(() => {
            if (currentIndex < questions.length - 1) {
                setCurrentIndex(prev => prev + 1);
            } else {
                finishQuiz();
            }
        }, 500);
    };

    const finishQuiz = async () => {
        setIsFinished(true);
        const userStr = localStorage.getItem("user");
        if (userStr) {
            try {
                const user = JSON.parse(userStr);
                if (user.id) {
                    await submitQuizResult(user.id, moduleId, score, questions.length, answers);
                }
            } catch (e) {
                console.error("Failed to save progress", e);
            }
        }
    };

    if (isFinished) {
        return (
            <div className="flex flex-col items-center justify-center min-h-[60vh] text-center space-y-6">
                <motion.div
                    initial={{ scale: 0 }}
                    animate={{ scale: 1 }}
                    className="p-6 rounded-full bg-navy-800 border-4 border-gold-500 shadow-2xl shadow-gold-500/20"
                >
                    <CheckCircle className="w-16 h-16 text-gold-500" />
                </motion.div>
                <h2 className="text-3xl font-bold text-steel-100 font-heading">Module Completed</h2>
                <div className="text-6xl font-bold text-gold-500">
                    {Math.round((score / questions.length) * 100)}%
                </div>
                <p className="text-steel-200">
                    You scored {score} out of {questions.length}
                </p>
                <button
                    onClick={() => router.push('/modules')}
                    className="px-8 py-3 bg-navy-800 hover:bg-navy-700 text-gold-500 border border-gold-600 rounded-lg uppercase tracking-wider transition-colors"
                >
                    Return to Modules
                </button>
            </div>
        );
    }

    return (
        <div className="w-full max-w-3xl mx-auto">
            {/* Header / Progress */}
            <div className="mb-8 flex flex-col gap-4">
                <div className="flex justify-between text-xs text-steel-300 uppercase tracking-widest font-bold">
                    <span>Question {currentIndex + 1} / {questions.length}</span>
                    <span>Module: {moduleId}</span>
                </div>
                {/* Progress Bar */}
                <div className="h-2 bg-navy-800 rounded-full overflow-hidden border border-navy-700">
                    <motion.div
                        className="h-full bg-gradient-to-r from-gold-600 to-gold-400"
                        initial={{ width: 0 }}
                        animate={{ width: `${progress}%` }}
                        transition={{ duration: 0.5 }}
                    />
                </div>
            </div>

            {/* Question Card */}
            <AnimatePresence mode="wait">
                <motion.div
                    key={currentIndex}
                    initial={{ x: 20, opacity: 0 }}
                    animate={{ x: 0, opacity: 1 }}
                    exit={{ x: -20, opacity: 0 }}
                    transition={{ duration: 0.3 }}
                    className="bg-navy-800/80 backdrop-blur-md border border-navy-600 p-8 rounded-2xl shadow-xl"
                >
                    <h3 className="text-xl md:text-2xl font-semibold text-steel-100 mb-8 leading-relaxed">
                        {currentQuestion.text}
                    </h3>

                    <div className="space-y-3">
                        {currentQuestion.options.map((option, idx) => (
                            <button
                                key={idx}
                                onClick={() => handleAnswer(idx)}
                                className="w-full text-left p-4 rounded-xl bg-navy-900/50 border border-navy-700 hover:border-gold-500/50 hover:bg-navy-700 transition-all group flex items-center justify-between"
                            >
                                <span className="text-steel-200 group-hover:text-white transition-colors">
                                    {String.fromCharCode(65 + idx)}. {option}
                                </span>
                                <div className="w-4 h-4 rounded-full border border-navy-500 group-hover:border-gold-500 transition-colors" />
                            </button>
                        ))}
                    </div>
                </motion.div>
            </AnimatePresence>
        </div>
    );
}
